<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>设备场景管理</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:page:add')">新增</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:page:modify')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:page:delete')">删除</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:page:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                </div>
            </div>
            <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
            <div v-show="inforadmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                    <div class="form-group">
                        <label class="control-label col-sm-2">单位名称</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.unit_name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="'请输入单位名称,最多为30位'" v-model="addData.unit_name" :data-vv-as="'单位名称'" v-validate="'required'">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <div class="form-group">
                         <label class="control-label col-sm-2">场景类型</label>
                        <div class="col-sm-6" >
                            <select type="text" name="url" class="form-control"  v-model="addData.type_id"><option :value="item.typeId" v-for="(item,index) of scenetypes" :key="index" v-text="item.name"></option> </select></div>
                      </div>
                      <div class="form-group">
                        <label class="control-label col-sm-2">具体地址</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('address')&&addData.address!='','has-error':errors.has('address')}">
                            <input type="text" class="form-control" name="address" :placeholder="'请输入具体地址'" v-model="addData.address" :data-vv-as="'具体地址'" v-validate="'required'">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('address')">{{errors.first("address")}}</span>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">地址说明</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('interpret')&&addData.interpret!='','has-error':errors.has('interpret')}">
                            <textarea rows="6" class="form-control" name="interpret" :placeholder="'请输入地址说明'" v-model="addData.interpret" :data-vv-as="'地址说明信息'" v-validate="'required'"></textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('interpret')">{{errors.first("interpret")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>
场景地址
<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            inforadmin: false,
            opreate: "add",
            tableSelectData: {
                selectTableurl: "/equipment/selectSceneAddress.action",
                pageloginData: {
                    id: this.stateid
                },
                filterName: []
            },
            addData: {
                unit_name: "",
                address: "",
                type_id: "",
                other:"",
                interpret:""
            },
            scenetypes:[]
        }
    },
    props: {
        stateid: String,
        statetitle: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        }
    },
    components: {
        tables,
    },
    mounted() {
        this.loginSceneType();
    },
    methods: {
        //添加,返回按钮按下
        inforAddClick(bool) {
            this.inforadmin = bool;
            this.opreate = "add";
            for(var key in this.addData){
                this.addData[key]="";
            }
            this.$validator.errors.clear();
        },
        //获取场景类型
        loginSceneType(){
            var vm=this;
            vm.$axios.post(vm.HOST+"/equipment/selectScene.action").then(function(data){
                if(data.bool){
                    vm.scenetypes=data.data.list;
                }
            }).catch(function(error){console.log(error)})
        },
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck(vm, "修改")) {
                this.inforadmin = true;
                this.opreate = "update";
                this.addData = this.checkData[0];
            }
        },
        //添加函数
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData;
            var datas = {
                data: data,
                states: {
                    id: stateId
                }
            };
            // 修改操作
            var bool;
            if (vm.opreate == "update") {
               bool = vm.$api.http.opreateHttp(vm, "/equipment/modifySceneAddress.action", datas, "修改", "");
            } else if (vm.opreate == "add") {
                //添加操作
                 bool = vm.$api.http.opreateHttp(vm, "/equipment/addSceneAddress.action", datas, "添加", "");
            }
            setTimeout(function () {
                vm.$validator.errors.clear();
            }, 300)
            bool.then(function (success) {
                if (success) {
                    setTimeout(function () {
                        vm.inforAddClick(false);
                    }, 800);
                }
            })
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
            if (vm.$api.opreate.TESTcheck(vm, "删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].id,
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            vm,
                            "/equipment/deleteSceneAddress.action",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入场景地址名后搜索", {
                    icon: 0
                });
            }
        }
    }
};
</script>

<style>

</style>



